




























































import {mapState, mapMutations, mapGetters} from 'vuex'

export default {
    computed: {
        ...mapState(['friends', 'hasLogin'])
    },
    data() {
        return {}
    },
    onReady() {
    },
    onNavigationBarButtonTap(e) {
        this.toaddfriend()
    },
    onLoad() {
    },
    methods: {
        ...mapMutations(['getActiveId']),
        toaddfriend() {
            uni.navigateTo({
                url: '../addfriend/addfriend'
